.charItem {
  position: relative;
  img {
    max-width: 100%;
  }
}

.charItemImgWrap {
  position: relative;
  height: 24.5vw;
  overflow: hidden;
  box-shadow: 0 0 20px #4c4b4bab;
}

.charItemImg {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  transition: all ease-in-out 0.4s;
}

.charItemName {
  color: #000;
  font-size: 1.2rem;
  font-weight: 600;
  transition: all ease-in-out 0.4s;
}

.charItemDescr {
  position: relative;
  color: #f9f9f9;
  font-size: 1.3rem;
  &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background-color: #f9f9f9;
    bottom: -10px;
    left: 0;
  }
}

.charItemMediaInfo span {
  color: #f9f9f9;
  font-size: 1.3rem;
  padding: 7px;
}

.charItemInfo {
  z-index: 2;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}

.charItemImgWrap::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: #0006;
  z-index: 1;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}

.charItem:hover {
  .charItemInfo {
    position: absolute;
    opacity: 1;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    height: fit-content;
    width: fit-content;
    padding: 16px;
  }
  .charItemName {
    color: #eb2328;
  }
  .charItemImg {
    transform: scale(1.25) rotate(2deg);
  }
  .charItemImgWrap::before {
    opacity: 1;
  }
}
